import React from 'react';
import Title from './title';
import Description from './description';
import Rating from './rating';

class Review extends React.Component {
render() { 
return ( 
<div>
Review components builiding block:
like Title, Description, Rating:
Will come here!
<br></br>
<b>Title:</b> <Title mytitle={this.props.title}></Title>
<br></br>
<b>Description:</b> <Description mydesc={this.props.desc}></Description><p></p>
<Rating myrating={this.props.rating}></Rating>
<hr></hr>
</div>
);

}
}

export default Review; 